.home {
  background-size: cover;
  background-color: aqua;
  flex-grow: 1;

  display: grid;
  grid-template-columns: 1fr 2fr;
}

.home .left {
  padding-left: 100px;
  padding-top: 100px;
}

.home h1 {
  margin-top: 0;
  font-size: 2.5rem;
  color: darkblue;
}

.home p {
  font-size: 1.2rem;
}

.home button {
  padding: 0;
  border: none;
  border-radius: 10px;
}

.home a {
  display: block;
  padding: 10px 20px;
  border-radius: 10px;
  background-color: black;
  color: white;
  text-decoration: none;
  font-size: 1.3rem;
}

.home a:hover {
  background-color: darkblue;
  color: yellow;
}

.home .right {
  text-align: center;
}

.home img {
  width: 50%;
}
